import React from 'react';

const Register = ( {onRouteChange} ) => {
return(

<article className="gradient-dark-blue mw5 center bg-purple br3 shadow-3 pa3 pa4-ns mv3 ba b--black-10">
<main className="pa4 black-80 gold">
  <form className="measure">
    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
      <legend className="f3 fw6 ph0 mh0">Register</legend>
      <div className="mt3">
        <label className="f3 db fw6 lh-copy" htmlFor="name">Name</label>
        <input className="pa2 input-reset ba bg-transparent hover-bg-purple hover-gold w-200" type="text" name="name"  id="name"/>
      </div>
      <div className="mt3">
        <label className="f3 db fw6 lh-copy" htmlFor="email-address">Email</label>
        <input className="pa2 input-reset ba bg-transparent hover-bg-purple hover-gold w-200" type="email" name="email-address"  id="email-address"/>
      </div>
      <div className="mv3">
        <label className="f3 db fw6 lh-copy" htmlFor="password">Password</label>
        <input className="b pa2 input-reset ba bg-transparent hover-bg-purple hover-gold w-100" type="password" name="password"  id="password"/>
      </div>
    </fieldset>
    <div className="">
      <input 
      onClick = {() => onRouteChange('signOut')}
      className=" f3 gold b ph3 pv2 input-reset ba b--black bg-transparent grow pointer dib" 
      type="submit" 
      value="Register"/>
    </div>
  </form>
</main>
</article>
  )
}

export default Register


